import Prompt from '../../../components/zz-prompt/index.vue'
//1引入组件 uni-load-more.vue
import uniLoadMore from '../../../components/uni-load-more.vue';
import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
var _self,
	page = 1,
	timer = null;
//定义全局参数,控制数据加载

export default {
	components: { //2注册组件
		uniLoadMore,
		mSearch,
		Prompt
	},

	data() {
		return {
			uuid: '', //UUID
			newsList: [],
			loadingText: '加载中...',
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},

	onPullDownRefresh: function() {
		//下拉刷新的时候请求一次数据
		_self.producelogQueryList()
	},
	onReachBottom: function() {
		//触底的时候请求数据，即为上拉加载更多
		//为了更加清楚的看到效果，添加了定时器
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_self.getmorenews();
		}, 1000);

		// 正常应为:
		// _self.getmorenews();
	},

	onLoad: function(option) {
		_self = this;
		//页面一加载时请求一次数据
		this.uuid = option.id
		_self.producelogQueryList()

	},
	methods: {
		getmorenews: function() {
			if (_self.loadingType !== 0) { //loadingType!=0;直接返回
				return false;
			}
			_self.loadingType = 1;
			uni.showNavigationBarLoading(); //显示加载动画
			//我的派工单信息分页查询
			this.$http.get(this.$store.state.producelogQuery, {
				dispatchUuid: this.uuid,
				pageSize: 10,
				pageNum: 1
			}).then(res => {

				page++; //得到数据之后page+1

				let num = _self.newsList.length //判断数据数组长度是否超过数据的总条目数
				if (num < res.data.totalRecord) {
					res.data.list.forEach(item => {
						_self.newsList.push(item);
					})
					_self.loadingType = 0; //将loadingType归0重置
				} else {
					_self.loadingType = 2; //没有数据
					uni.hideNavigationBarLoading(); //关闭加载动画
				}


				uni.hideNavigationBarLoading(); //关闭加载动画
				console.log(res)
			})

		},



		producelogQueryList() {
			//生产日志分页查询明细
			page = 1;
			this.loadingType = 0;
			uni.showNavigationBarLoading();
			this.loadingType = 0;
			uni.showNavigationBarLoading();


			this.$http.get(this.$store.state.producelogQuery, {
				dispatchUuid: this.uuid,
				pageSize: 10,
				pageNum: page,
				pageSize: 10,
				receiveState: 1,
			}).then(res => {
				page++; //得到数据之后page+1
				_self.newsList = res.data.list;
				uni.hideNavigationBarLoading();
				uni.stopPullDownRefresh(); //得到数据
				console.log(res)

				let num = _self.newsList.length //判断数据数组长度是否超过数据的总条目数
				if (num == res.data.totalRecord) {
					_self.loadingType = 2; //没有数据
					uni.hideNavigationBarLoading(); //关闭加载动画
				}
			})
		}
	},

}
